/* https://designmodo.com/3d-css3-button/ */
.btn {
  width: 190px;
  height: 60px;
  /* margin-right: 4%;*/
  margin-bottom: 4%;
  font-size: 1.4em;
  color: #faf8f8;
  letter-spacing: 0.1em;
  cursor: pointer;
  border-radius: 10px;
  transition: all .3s;
  border: 1px solid rgba(240, 134, 136, 0.911);
  text-shadow: 0px -0.2px 0px #a1a1a179;
  box-shadow: 2px 4px 0px 0px #8f8d8da2, 2px 4px 10px 0px rgba(87, 87, 87, 0.63), inset 0px 1px 0px rgba(128, 115, 115, 0.473), inset 0px 0px 3px rgba(194, 192, 192, 0.5);
  transition: all .4s ease-in-out;
  background-color: rgba(242, 76, 79, 0.719);
  background-image: linear-gradient(to bottom, rgba(245, 84, 87, 0.747) 0%, rgb(248, 134, 136) 100%);
}


.btn:hover {
	box-shadow: 0px 15px 25px -5px rgba(darken(rgb(105, 94, 70), 40%));
	-webkit-transform: scale(1.05);
	        transform: scale(1.05)
}
.btn:active {
	background-image: linear-gradient(to bottom, rgba(240, 38, 41, 0.74) 0%, rgba(242, 76, 79, 0.719) 100%);
	box-shadow: 0px 5px #2b638f, 0px 1px rgba(0,0,0,.4), inset 0px 1px 0px rgba(255,255,255,.3), inset 0px 0px 3px rgba(255,255,255,.5);
	-webkit-transform: translate(0, 4px);
	        transform: translate(0, 4px);
	
}

