p.quote_text {
	position: relative;
	width: 350px;
	height: auto;
	min-height: 200px;
	padding: 1.5em;
	background: #FFFFFF;
	border-radius: 10px;
	border: #898989 solid 3px;
	margin-left: 80px;
	font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
	font-size: 25px;
	overflow-wrap: break-word;
}
p.quote_text:after {
	content: '';
	position: absolute;
	border-style: solid;
	border-width: 15px 15px 15px 0;
	border-color: transparent #FFFFFF;
	display: block;
	width: 0;
	z-index: 1;
	left: -15px;
	top: 33px;
}
p.quote_text:before {
	font-size: 10em;
	content: '';
	position: absolute;
	border-style: solid;
	border-width: 17px 17px 17px 0;
	border-color: transparent #898989;
	display: block;
	width: 0;
	z-index: 0;
	left: -20px;
	top: 31px;
}


